/* global css */
.pane {
    background: #fff;
    border-style: solid;
    border-color: #fff;
    border-width: 1px;
    /* margin-top: 4px;
    margin-bottom: 4px;
    margin-left: 2px;
    margin-right: 2px; */
    border-radius: 5px;
    padding: 5px;
    /* box-shadow:  0px 0px 4px 1px #dddddd; */
}

.header {
    background-color: #eee;
    margin: 5px;
    padding-left: 10px;
    padding-top: 4px;
    border-radius: 5px;
    height: 40px;
    font-size: 20px;
    font-family: 'Avenir Heavy', Arial, Helvetica, sans-serif;
}

.ant-btn-link {
    color:  #00aeff !important;
}

@font-face {
    font-family: 'iconfont';  /* project id 1938658 */
    src: url('//at.alicdn.com/t/font_1938658_n3uawae8v9a.eot');
    src: url('//at.alicdn.com/t/font_1938658_n3uawae8v9a.eot?#iefix') format('embedded-opentype'),
    url('//at.alicdn.com/t/font_1938658_n3uawae8v9a.woff2') format('woff2'),
    url('//at.alicdn.com/t/font_1938658_n3uawae8v9a.woff') format('woff'),
    url('//at.alicdn.com/t/font_1938658_n3uawae8v9a.ttf') format('truetype'),
    url('//at.alicdn.com/t/font_1938658_n3uawae8v9a.svg#iconfont') format('svg');
  }
.iconfont {
    font-family: "iconfont" !important;
    font-size: 25px;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

/*定义滚动条高宽及背景
 高宽分别对应横竖滚动条的尺寸*/
 ::-webkit-scrollbar {
    width: 4px;
    height: 4px;
    /*background-color:#F5F5F5;*/
}

/*定义滑块
 内阴影+圆角*/
::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color: #555;
    margin-top: 2px;
}
.ant-divider-inner-text{
    color:#968176;
}

